<template>
    <div class="home h">
      <el-container class="h">
          <left-side @showmsg="getcomponentmsg" :msg="msg"></left-side>
          <el-main>
            <div class="breadcrumb">
              <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
              </el-breadcrumb>
            </div>

            <div class="content">
              <h2>{{ title }}</h2>
              <p>
                <span>{{ author }}</span>
                <span>{{ publishDate }}</span>
                <span>{{ viewCount }}</span>
              </p>
              <div class="con" v-html="content"></div>
              <div class="rate">
                <el-rate
                  v-model="rate"
                  show-text>
                </el-rate>
              </div>
            </div>
            <foot></foot>
          </el-main>
      </el-container>
    </div>
</template>

<script>
import leftSide from '@/components/aside.vue'
import foot from '@/components/footer.vue'

export default {
  name: 'Single',
  data () {
    return {
      msg: '',
      id: '',
      title: '',
      author: '',
      publishDate: '',
      viewCount: '',
      content: '',
      fullscreenLoading: true,
      rate: null
    }
  },
  components: {
    leftSide,
    foot
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    async fetchData () {
      this.id = this.$route.params.id // URLid
      //   TODO
      // mock
      switch (this.id) {
        case 1:
          this.title = 'Vue'
          break
        case 2:
          this.title = 'PDF.jsPDFObject.js'
          break
        case 3:
          this.title = 'Echarts'
          break
        case 4:
          this.title = 'js demo'
          break
      }
      this.author = 'haibao'
      this.publishDate = '2018-08-13'
      this.viewCount = '200'
      this.content = '<strong>ID</strong>' + this.id
    },
    getcomponentmsg (msg) {
      alert('-----' + msg)
    }
  }
}
</script>

<style scoped>
.breadcrumb {
  background-color: #f2f2f2;
  padding: 10px;
}
.content {
  padding: 25px 0;
  min-height: 450px;
}
.rate {
  margin-top: 20px;
}
</style>



